import { prefix } from '../../globals/settings';
import { html, LitElement } from 'lit-element';
import { carbonElement as customElement } from '../../globals/decorators/carbon-element';
import styles from './kase-header.scss';
import Avatar16 from '@carbon/icons/lib/user--avatar--filled/16';
import Avatar20 from '@carbon/icons/lib/user--avatar--filled/20';
import Logout16 from '@carbon/icons/lib/logout/16';

import '../ui-shell';
import '../dropdown';
import '../turn-avatar';

@customElement(`${prefix}-kase-header`)
export class GlobalHeader extends LitElement {
  static styles = styles;

  constructor() {
    super();
    this.handleEscapeKey = this.handleEscapeKey.bind(this);
  }

  connectedCallback() {
    super.connectedCallback();
    document.addEventListener('keydown', this.handleEscapeKey);
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    document.removeEventListener('keydown', this.handleEscapeKey);
  }

  handleEscapeKey(event) {
    if (event.key === 'Escape') {
      //@ts-ignore
      const popovers = this.shadowRoot.querySelectorAll(
        'cds-popover[tabTip][open]'
      );
      popovers.forEach((popover) => popover.removeAttribute('open'));
    }
  }

  render() {
    const handleClick = (id) => {
      //@ts-ignore
      const popover = this.shadowRoot.querySelector(id);
      const open = popover?.hasAttribute('open');
      open
        ? popover?.removeAttribute('open')
        : popover?.setAttribute('open', '');
    };
    return html`
      <link
        rel="stylesheet"
        href="https://demo.turn.lv/kase/dist/assets/css/themes.css" />
      <div>
        <cds-header
          aria-label="IBM Platform Name"
          role="banner"
          class="cds-theme-zone-kase-header">
          <cds-header-menu-button
            button-label-active="Close menu"
            button-label-inactive="Open menu"></cds-header-menu-button>
          <cds-header-name href="#">Valsts Kase</cds-header-name>
          <span class="header-nav-menu-container"
            ><slot name="header-nav-content"></slot
          ></span>
          <div class="cds--header__global">
            <slot name="global-action"></slot>
            <div
              class="popover-tabtip-story cds-theme-zone-kase-header-popover popover"
             >
              <cds-popover tabTip id="popover-two" align="bottom-right">
                <cds-button
                class="popover__button"
                  aria-label="Iziet"
                  tooltip-text="Iziet"
                  tooltip-position="bottom"
                  @click="${() => handleClick('#popover-two')}">
                  <span class="popover__button--icon" slot="icon">${Avatar20({
                    fill: 'white',
                  })}</span>
                </cds-button>

              
                <cds-popover-content>
                <cds-item-padding-container vertical="03">
                  <div class="popover__person">
                    <p><slot name="user-name-right-panel"></slot></p>
                    <span>${Avatar16()}</span>
                  </div>
                  <cds-item-padding-container vertical="03">
                  <cds-switcher-item>Maini Data</cds-switcher-item>
                  </cds-item-padding-container>
                  <cds-switcher-divider></cds-switcher-divider>
                  <cds-switcher-item
                    >Iziet
                    <span class="popover__logout"
                      >${Logout16()}</span
                    ></cds-switcher-item
                  >
                </cds-popover-content>
              </cds-popover>
            </div>
          </div>
        </cds-header>
        <cds-side-nav
          aria-label="Side navigation"
          collapse-mode=""
          class="cds-theme-zone-kase-header-sidebar">
          <cds-side-nav-items>
            <slot name="sidebar-mobile-nav-content"></slot>
            <slot name="sidebar-content"></slot>
          </cds-side-nav-items>
        </cds-side-nav>
      </div>
    `;
  }
}
